.inner,
.inner-lg,
.inner-lg-pl {
  max-width: 1340px;
  width: 100%;
  margin: 0 auto;
  padding: 0 30px;
  @include lg {
    padding: 0 60px; }
  @include md {
    padding: 0 30px; } }
.inner-lg-pl {
  padding-left: 148px;
  @include lg {
    padding-left: 10.9385%; } }
.inner {
  padding: 0 150px;
  @include lg {
    padding: 0 10.9385%; } }

.row {
  display: flex;
  flex-wrap: wrap;
  // margin: 0 -60px
  @include lg {
 }    // margin: 0 -50px
  @include sm {
    margin: 0 auto; } }

.col {
  @include flex-size(50%);
  &:nth-child(even) {
    padding-left: 60px;
    @include lg {
      padding-left: 50px; }
    @include sm {
      padding-left: 0; } }
  &:nth-child(odd) {
    padding-right: 60px;
    @include lg {
      padding-right: 50px; }
    @include sm {
      padding-right: 0; } }
  // padding-left: 60px
  // padding-right: 60px
  @include lg {
    // padding-left: 50px
 }    // padding-right: 50px
  @include sm {
    @include flex-size(100%);
    padding: 0; } }
